$color: #fff;
$font: "Antonio";
$font2: "Titillium Web";
main {
  width: 100%;
  max-width: 100%;
}
.inicio-main {
  background-color: rgb(5, 104, 198);
  height: 90vh;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  padding-left: 2rem;
}
.inicio-main h2 {
  font-size: 3rem;
  font-family: $font;
  font-weight: 700;
  color: $color;
  text-transform: uppercase;
  margin-bottom: 0;
}
.inicio-main p {
  font-size: 1rem;
  color: $color;
  font-family: sans-serif;
}
.inicio-main button {
  padding: 1rem 1.3rem;
  border: none;
  background-color: $color;
  font-size: 1rem;
  font-family: $font;
  font-weight: 700;
  transition: 0.5s ease all;
  &:hover {
    opacity: 0.9;
  }
}
.main-off {
  background-color: rgb(22, 242, 136);
  width: 100%;
  padding: 3rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  font-family: sans-serif;
  color: $color;
}
.main-off h2 {
  font-size: 2rem;
  font-family: $font2;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 0;
}
.main-off p {
  width: 35rem;
  font-size: 1.1rem;
  font-family: sans-serif;
  text-align: center;
}
.main-off a {
  text-decoration: none;
  color: $color;
  font-size: 1.2rem;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: underline;
  margin-bottom: 0.9rem;
}
.main-top {
  width: 100%;
  margin-bottom: 5rem;
}
.main-top h2 {
  font-family: sans-serif;
  font-size: 1.8rem;
  margin-left: 1rem;
}
.main-top .product-top article {
  margin: 0 1rem;
  width: 24.3rem;
  height: 7rem;
  cursor: pointer;
  h2 {
    font-size: 2rem;
    font-weight: 700;
  }
  border-bottom: 1px solid black;
  &:hover {
    border-bottom: 8px solid #000;
  }
}
.main-top .product-top {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.main-buy {
  width: 100%;
  margin-bottom: 5rem;
  h2 {
    font-family: sans-serif;
    font-size: 1.8rem;
    margin-left: 1rem;
  }
  .section-buy {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    figure {
      width: 25rem;
      margin: 0 0.5rem;
      height: 30rem;
      cursor: pointer;
      img {
        width: 100%;
        height: 89%;
        object-fit: cover;
      }
      figcaption {
        font-size: 1.4rem;
        font-weight: 700;
        font-family: $font2;
        margin-top: 1rem;
      }
    }
  }
}
.main-tendencia {
  width: 100%;
  margin-bottom: 5rem;
  position: relative;
  .next-top {
    position: absolute;
    font-size: 2.4rem;
    background-color: transparent;
    border: none;
    right: 5rem;
    top: 0;
    color: #000;
  }
  .prev-top {
    position: absolute;
    font-size: 2.4rem;
    background-color: transparent;
    border: none;
    right: 2rem;
    top: 0;
    color: #000;
  }
  .all {
    position: absolute;
    font-size: 1.4rem;
    font-family: $font2;
    font-weight: 700;
    background-color: transparent;
    text-decoration: underline;
    text-transform: uppercase;
    border: none;
    right: 9.5rem;
    top: 0;
    color: #000;
  }
  h2 {
    font-family: sans-serif;
    font-size: 1.8rem;
    margin-left: 1rem;
    i {
      font-size: 2rem;
      height: 1rem;
    }
  }
  .contenedor {
    width: 100%;
    overflow: auto;
    scroll-behavior: smooth;
    &::-webkit-scrollbar {
      height: 0px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: transparent;
    }
    .contenedor-buy {
      display: flex;
      flex-wrap: nowrap;
      article {
        position: relative;
        min-width: 23%;
        width: 23%;
        height: 26rem;
        margin: 0 0.4rem;
        &:hover {
          border: 1.5px solid #000;
        }
        span {
          position: absolute;
          left: 0.5rem;
          font-family: sans-serif;
          background: #fff;
          padding: 0.1rem 0.1rem;
          bottom: 7.8rem;
          transition: 0.1s ease all;
          &:hover {
            bottom: 8rem;
          }
        }
        p {
          margin: 0.3rem 0.7rem;
        }
        .texto {
          margin-bottom: 0;
          font-family: $font2;
          font-size: 1rem;
          color: #000;
        }
        .name {
          color: #00000080;
        }
        .enviar {
          font-size: 0.9rem;
          font-family: $font2;
        }
        img {
          object-fit: cover;
          width: 100%;
          height: 70%;
        }
      }
    }
  }
}
.section-novedades {
  position: relative;
  width: 100%;
  margin-bottom: 0;
  h2 {
    font-family: sans-serif;
    font-size: 1.8rem;
    margin-left: 1rem;
    font-style: italic;
  }
  .contenedor-r {
    width: 100%;
    overflow: hidden;
    scroll-behavior: smooth;
    section {
      display: flex;
      flex-wrap: nowrap;
      article {
        margin: 0 0.7rem;
        min-width: 23%;
        height: 35rem;
        img {
          width: 100%;
          height: 70%;
          object-fit: cover;
        }
        h3 {
          font-family: $font2;
          font-size: 1rem;
          margin-bottom: 0.3rem;
        }
        p {
          margin-top: 0;
          margin-bottom: 2rem;
          font-family: $font2;
          font-size: 1rem;
        }
        b {
          font-family: $font2;
          font-size: 1rem;
          text-transform: uppercase;
          border-bottom: 2px solid black;
          cursor: pointer;
          &:hover {
            background-color: #000;
            color: $color;
            border: none;
          }
        }
      }
    }
  }
}
.next {
  position: absolute;
  right: 0.5rem;
  top: 40%;
  font-size: 2.3rem;
  padding: 0.1rem 0.3rem;
  border: 1px solid #000;
}
.prev {
  position: absolute;
  left: 0.5rem;
  top: 40%;
  font-size: 2.3rem;
  padding: 0.1rem 0.3rem;
  border: 1px solid #000;
}
.section-article {
  width: 100%;
  height: 20rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-image: url("./images/paisaje.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-bottom: 5rem;
  h2 {
    width: 40rem;
    text-align: center;
    font-family: sans-serif;
    font-size: 1.8rem;
    margin-left: 1rem;
    color: $color;
    margin-bottom: 1rem;
  }
  button {
    font-size: 1.5rem;
    text-transform: uppercase;
    font-family: $font;
    padding: 0.5rem;
    background-color: #000;
    border: 1px solid #fff;
    color: $color;
  }
}
@media (max-width: 850px) {
  .inicio-main{
    padding-left:.5rem;
  }
  .main-tendencia .contenedor .contenedor-buy article {
    min-width: 30%;
    width: 30%;
  }
  .all {
    display: none;
  }
  .section-novedades .contenedor-r section article {
    margin: 0;
    min-width: 30%;
    width: 30%;
  }
  .main-off p{
    width: 100%;
  }
}
@media (max-width: 650px) {
  .inicio-main{
    padding-left:.5rem;
  }
  .main-tendencia .contenedor .contenedor-buy article {
    min-width: 80%;
    width: 80%;
  }
  .section-novedades .contenedor-r section article {
    margin: 0;
    min-width: 100%;
    width: 100%;
  }
  .section-article h2{
  width: 20rem;
  }
}
