body {
  margin: 0;
  padding: 0;
  width: 100%;
}
html {
  box-sizing: border-box;
  font-size: 16px;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
header {
  width: 100%;
  max-width: 100%;
}
main {
  width: 100%;
  max-width: 100%;
}
.menu {
  display: none;
}
.menu-close{
  display: none;
}
.nav-header {
  width: 100%;
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  height: 4.5rem;
  padding: 0 2rem;
}

.logo {
  width: 5rem;
  object-fit: cover;
  display: block;
  margin: auto 0;
}
.enlaces-header a {
  color: #000;
  text-decoration: none;
  margin: 0 0.6rem;
  font-size: 1.3rem;
  font-family: "Antonio", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}
.enlaces-header a:hover {
  border-bottom: 3px solid #000;
}
.header-search input[type="search"] {
  padding: 0.5rem;
  outline: none;
  height: 2rem;
  width: 10rem;
  background-color: #c5c5c550;
  border: none;
  font-family: sans-serif;
  font-weight: 400;
  margin-right: 1rem;
}
.header-search input[type="search"]::placeholder {
  font-weight: 600;
}
.header-search i {
  font-size: 1.5rem;
  margin-right: 1rem;
  font-weight: 700;
}
.section-about {
  width: 100%;
  background-color: #b8b8b825;
}
.section-about section {
  width: 100%;
  padding: 2rem 0;
  background-color: #b8b8b825;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.section-about section article {
  width: 45%;
  margin: 0 1.3rem;
}
.section-about section article h2 {
  width: 20rem;
  font-family: sans-serif;
  font-size: 2rem;
  font-weight: 700;
}
.section-about section article p {
  font-family: sans-serif;
  font-size: 1.3rem;
}
.section-login {
  width: 100%;
}
.section-login div {
  width: 100%;
  background-color: rgb(254, 224, 29);
  padding:  2.6rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.section-login div h2 {
  width: 30rem;
  font-size: 2rem;
  font-family: sans-serif;
  text-transform: uppercase;
  margin-right: 1.2rem;
}
.section-login div button {
  height: 2.9rem;
  width: 13rem;
  border: 1px solid #fff;
  font-size: 1rem;
  text-transform: uppercase;
  background-color: #000;
  color: #fff;
}
.section-contacto {
  width: 100%;
}
.section-contacto section {
  width: 85%;
  max-width: 85%;
  display: flex;
  margin-left: 3rem;
  justify-content: center;
  padding: 2rem 0;
}
.section-contacto section aside h2 {
  font-family: sans-serif;
  font-size: 1.3rem;
  text-transform: uppercase;
}
.section-contacto section aside {
  margin: 0 2rem;
}
.section-contacto section aside p {
  font-family: sans-serif;
  margin: 0.3rem 0;
  font-size: 0.9rem;
  color: rgba(0, 0, 0, 0.775);
  cursor: pointer;
}
.section-contacto section aside p:hover {
  text-decoration: underline;
}
.section-contacto section aside a {
  display: block;
  margin: 0.3rem 0;
  font-size: 1.7rem;
}
.footer {
  padding: 1.5rem 0;
  width: 100%;
  max-width: 100%;
  background-color: #000;
}
.footer .ubicacion {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  color: white;
}
.footer .ubicacion a {
  margin: 0 1rem;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.804);
}
.footer .small {
  margin-top: 2rem;
  margin-left: 10rem;
}
.footer .small small {
  margin-top: 3rem;
  font-family: sans-serif;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.804);
}
@media screen and (max-width: 970px) {
  .nav-header {
    justify-content: space-between;
  }
  .enlaces-header {
    position: fixed;
    width: 100%;
    max-width: 100%;
    height: 100%;
    background-color: white;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    transition:.7s ease all;
  }
  .enlaces-header.visible-menu{
    transform: translateX(0);
  }
  .enlaces-header.hidden-menu{
    transform: translateX(-100%);
  }
  .header-search input[type="search"]{
    display: none;
  }
  .menu-close {
    display: block;
    border: none;
    font-family: sans-serif;
    font-size: 2.3rem;
    background: transparent;
    position: absolute;
    top: .4rem;
    right: .3rem;
  }
  .enlaces-header a {
    margin: 1rem 1.5rem;
  }
  .menu {
    display: block;
  }
  .section-about section article{
    width: 100%;
    margin: .4rem;
  }
  .section-login div h2 {
    width: 100%;
    text-align: center;
  }
  .section-contacto {
    display: none;
  }
  .footer .small{
    margin: 0;
  }
}
